
import Vue from 'vue'
import VueHtml5Editor from 'vue-html5-editor'
import XInput from 'vux/src/components/x-input/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'
import XDialog from 'vux/src/components/x-dialog/index.vue'

import axios from 'axios'
import { getItem } from '../../utils/storage'
import { fileInfoTran } from '../../utils/dataTran'

export default {
name: 'h5Editor',
data () {
return {
item: {},
options: [{
key: 0,
value: ''
}, {
key: 1,
value: ''
}],
show: false,
show1: false,
opt: 0,
userId: getItem('user').id,
multiVer: false,
everVer: [],
change: false
}
},
components: {
XInput,
XHeader,
PopupHeader,
Popup,
Group,
Radio,
XDialog
},
mounted () {
const { fileId } = this.$route.params
console.log(fileId)
!fileId
? this.item = {
title: '',
content: '',
type: 0,
fileType: 'md',
id: ''
}
: axios.get(`/api/getfileinfo`, {
params: {
fileId
}})
.then((res) => {
if (res.data.status) {
const data = fileInfoTran(res.data.list)
this.item = data[0]
if (res.data.list.length > 1) {
this.multiVer = true
this.everVer = data.slice(1)
}
console.log(this.everVer)
} else {
AlertModule.show({
content: res.data.msg
})
}
}).catch((error) => {
console.log(error)
})
console.log(this.item)
},
methods: {
save () {
this.item.id === '' ? this.show = true : this.saveFile()
},
saveFile () {
this.show = false
const body = {
...this.item,
userId: this.userId,
fileId: this.item.id,
title: this.item.title === '' ? '' : this.item.title,
type: this.item.id === '' ? this.opt : this.item.type
}
this.change ? axios
.post(`/api/savefile`, body)
.then((res) => {
const opt = res.data.status && res.data.isDiff ? {
name: 'noteShow',
params: {
item: fileInfoTran([res.data.fileInfo], true, res.data.isDiff)
}
} : {
path: '/note/list'
}
this.$router.push(opt)
}).catch((error) => {
console.log(error)
}) : this.$router.push({
path: '/note/list'
})
},
retVer (index) {
this.item = this.everVer[index]
}
}
}
Vue.use(VueHtml5Editor, {

name: 'vue-html5-editor',


showModuleName: false,


icons: {
text: 'fa fa-pencil',
color: 'fa fa-paint-brush',
font: 'fa fa-font',
align: 'fa fa-align-justify',
list: 'fa fa-list',
link: 'fa fa-chain',
unlink: 'fa fa-chain-broken',
tabulation: 'fa fa-table',
image: 'fa fa-file-image-o',
hr: 'fa fa-minus',
eraser: 'fa fa-eraser',
undo: 'fa-undo fa',
'full-screen': 'fa fa-arrows-alt',
info: 'fa fa-info'
},


image: {

sizeLimit: 512 * 1024,


upload: {
url: null,
headers: {},
params: {},
fieldName: {}
},



compress: {
width: 1600,
height: 1600,
quality: 80
},


uploadHandler (responseText) {

var json = JSON.parse(responseText)
if (!json.ok) {
alert(json.msg)
} else {
return json.data
}
}
},


language: 'zh-cn',

i18n: {

'zh-cn': {
'align': '',
'image': '',
'list': '',
'link': '',
'unlink': '',
'table': '',
'font': '',
'full screen': '',
'text': '',
'eraser': '',
'info': '',
'color': '',
'please enter a url': '',
'create link': '',
'bold': '',
'italic': '',
'underline': '',
'strike through': '',
'subscript': '',
'superscript': '',
'heading': '',
'font name': '',
'font size': '',
'left justify': '',
'center justify': '',
'right justify': '',
'ordered list': '',
'unordered list': '',
'fore color': '',
'background color': '',
'row count': '',
'column count': '',
'save': '',
'upload': '',
'progress': '',
'unknown': '',
'please wait': '',
'error': '',
'abort': '',
'reset': ''
}
},


hiddenModules: [],



visibleModules: [
'text',
'color',
'font',
'align',
'list',
'link',
'unlink',
'tabulation',
'image',
'hr',
'eraser',
'undo',
'full-screen',
'info'
],


modules: {

}
})



